export default {
	data() {
		return {
			baseUrl: '',
			years:[],
			yearss:[]
		};
	},
	onShow() {
		// console.log('now:' + this.websiteUrl);
		this.baseUrl = this.websiteUrl;
		// console.log(this.baseUrl);
		uni.setNavigationBarTitle({
			title: this.$t('index.pingtai')
		})
		uni.setTabBarItem({
			index:0,
		    text: this.i18n.navTitle
		});
		uni.setTabBarItem({
			index:2,
		    text: this.i18n.navTitleTwo
		});
		uni.setTabBarItem({
			index:1,
		    text: this.i18n.navTitleThree
		});
		uni.setTabBarItem({
			index:3,
		    text: this.i18n.navTitleFour
		});
	},
	onLoad() {
	uni.request({
		url: 'http://td.gfapki.com.cn:8090/country-indices',
		method: 'GET',
		success: res => {
			console.log(eval(res.data));
			// var data = eval('(' + JSON.stringify(res.data) + ')');
			//  console.log(Object.entries(res.data))
			// arr = eval(res.data);
			// this.years = res.data;
			// this.yearss = res.data;
			var arr = res.data;
			var arr1 = [];
			var arr2 = [];
			var arr3 = [];
			for (var key in arr) {
				arr1.push(arr[key]);
			}
			arr1.sort();
			for (var i = 0; i < arr1.length; i++) {
				for (var key in arr) {
					if (arr1[i] == arr[key]) {
						console.log('key' + key);
			
						arr2.push({ key: key, value: arr1[i] });
						arr3.push({ key: key, value: arr1[i] });
						break;
					}
				}
			}
			console.log(arr2);
			this.years = arr2;
			this.yearss = arr3;
			uni.setStorage({
				key:"country_one",
				data:this.years
			})
			uni.setStorage({
				key:"country_two",
				data:this.yearss
			})
		},
		fail: () => {},
		complete: () => {}
	});	
	},
	
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	methods: {
		change() {
			const system_info = uni.getStorageSync('system_info');
			system_info.language === 'en' ? (system_info.language = this._i18n.locale = 'zh_CN') : (system_info.language = this._i18n.locale = 'en');
			uni.setStorageSync('system_info', system_info);
			uni.reLaunch({
				url: 'index'
			});
			if(system_info.language === 'en'){
				uni.setNavigationBarTitle({
					title: this.$t('index.pingtai')
				})
				uni.setTabBarItem({
					index:0,
				    text: this.i18n.navTitle
				});
				uni.setTabBarItem({
					index:1,
				    text: this.i18n.navTitleTwo
				});
				uni.setTabBarItem({
					index:2,
				    text: this.i18n.navTitleThree
				});
				uni.setTabBarItem({
					index:3,
				    text: this.i18n.navTitleFour
				});
			}else {
				uni.setNavigationBarTitle({
					title: this.$t('index.pingtai')
				})
				uni.setTabBarItem({
					index:0,
				    text: this.i18n.navTitle
				});
				uni.setTabBarItem({
					index:1,
				    text: this.i18n.navTitleTwo
				});
				uni.setTabBarItem({
					index:2,
				    text: this.i18n.navTitleThree
				});
				uni.setTabBarItem({
					index:3,
				    text: this.i18n.navTitleFour
				});
			}
			
		},
		codeQuery() {
			uni.navigateTo({
				url: '../codeQuery/codeQuery'
			});
			// uni.navigateTo({ url: '../login/login'});
		},
		shoppingqQuery() {
			uni.navigateTo({
				url: '../miaoshuchaxun/miaoshuchaxun'
			});
		},
		zhuanYi() {
			uni.navigateTo({
				url: '../zhuanyi/zhuanyi'
			});
		},
		weiTuo(){
			uni.navigateTo({
				url: '../weituo/weituo'
			});
		}
	}
};
